export const typeDefs = `

  type User {
    _id: String
    avatar: String
    firstName: String
    lastName: String
    email: String
    fullname: String

  }

  input UserInput {
    avatar: String
    firstName: String
    lastName: String
    email: String
    fullname: String
  }



  type Query {
    user:User
  }
  type Mutation{
    saveUser(user:UserInput!)
  }


`;
