import { NgModule } from '@angular/core';
import { ApolloModule, APOLLO_OPTIONS } from 'apollo-angular';
import { HttpLinkModule, HttpLink } from 'apollo-angular-link-http';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { ApolloLink, split } from 'apollo-link';
import { stateLink } from './links/state';
import { authLink } from './links/auth';
import { wsLink } from './links/ws';
import { getMainDefinition } from 'apollo-utilities';

// const uri = 'http://localhost:7000/'; // <-- add the URL of the GraphQL server here
const uri = 'https://james-pweb-app.herokuapp.com/'; // <-- add the URL of the GraphQL server here

export function createApollo(httpLink: HttpLink) {
  const http = httpLink.create({ uri });
  // const link = ApolloLink.from([stateLink, authLink, http]);
  const link = split(
    ({ query }) => {
      const { kind, operation } = getMainDefinition(query);
      return kind === 'OperationDefinition' && operation === 'subscription';
    },
    wsLink,
    http
  );

  return {
    link: ApolloLink.from([stateLink, authLink, link]),
    cache: new InMemoryCache()
  };
}

@NgModule({
  exports: [ApolloModule, HttpLinkModule],
  providers: [
    {
      provide: APOLLO_OPTIONS,
      useFactory: createApollo,
      deps: [HttpLink]
    }
  ]
})
export class GraphQLModule {}
